@import "variables.scss";

.entry-content
{
    margin-left: 0;
    line-height: 1.4em;
    font-size: 1.5em;

    @media screen and (max-width: $layout-breakpoint-xs - 1px)
    {
        font-size: 1.3em;
    }

    p
    {
        margin-top: 30px;

        @media screen and (max-width: $layout-breakpoint-xs - 1px)
        {
            margin-top: 15px;
        }
    }

    h2
    {
        font-size: 1.3em;
        margin-top: 30px;
        font-weight: bold;
        border-bottom: 1px solid #ddd;

        &::before
        {
            border-bottom: none;
        }


    }

    .timespan
    {
        margin-top: -20px;
    }

    a:hover
    {
        text-decoration: underline;
    }

    blockquote
    {
        border-left: 5px solid $primary-color;
        background: #F7F7F7;
        padding: 15px 20px;
        margin: 0;

        p
        {
            margin: 0;
        }
    }

    table
    {
        line-height: 1.2em;

        td
        {
            text-align: left;
        }
    }

    .more-link
    {
        display: block;
        margin-top: 16px;
        padding-left: 30px;
        position: relative;

        &:before
        {
            line-height: 1.2em;
        }
    }

    .caption
    {
        display: block;
        font-style: italic;
        font-size: 0.6em;
        font-family: helvetica, arial, san-serif;
        color: #999;
        padding-left: 15px;
        position: relative;
        margin-top: -5px;

        &:before
        {
            content: "\f040";
            color: #ccc;
            font: 1.3em FontAwesome;
            line-height: 1.6em;
            position: absolute;
            left: 0;
        }
    }

    img:not(.emoji)
    {
        max-width: 100%;
        height: auto;
        box-shadow: 0 2px 4px 0 rgba(0,0,0,0.16), 0 2px 10px 0 rgba(0,0,0,0.12);
    }

    .video
    {
        width: 100%;
        position: relative;
        height: 0;
        padding-bottom: calc(100% / 16 * 9);

        &.video-4-3
        {
            padding-bottom: calc(100% / 4 * 3);
        }

        iframe
        {
            width: 100%;
            height: 100%;
            position: absolute;
            top: 0;
            left:0;
        }
    }
}
